.comp-info-p{
    display: inline-flex;
}

.comp-info-label{
    margin-right: 5px;
    font-weight: bold;
}

.comp-info-jumbo{
    margin-top: 2em;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    border-radius: 5px;
    background-color: rgba(106, 106, 255, 0.514);
}

.comp-info-div{
    margin-left: auto;
    margin-right: auto;
}

.comp-info-img-div{
    text-align: left;
}

.comp-info-img{
    height: 200px;
    width: 350px;
    border-radius: 3px;
}

.comp-info-title{
    text-align: left;
    font-weight: bold;
}

.comp-info-jumbo-btn-div{
    text-align: left;
    margin-left: 0.5em;
}

.comp-info-info{
    font-size: 25px;
}