.home-card-div{
    display: inline-flex;
    height: 320px;
    margin-left: 2em;
    margin-right: 2em;
    margin-top: 1em;
    margin-bottom: 1em;
}

.home-card-div:hover{
    background-color: rgba(0, 0, 0, 0.5);
}

.home-img-overlay:hover{
    background-color: rgba(0, 0, 0, 0.5);
}

.home-card-title{
    cursor: default;
    text-shadow: -1px 1px 2px #000, 1px 1px 2px #000, 1px -1px 0 #000, -1px -1px 0 #000;
}

.home-title-restaurant{
    font-family: 'Mate', serif;
    font-size: 80px;
    font-weight: bold;
    padding-top: 75px;
}

.home-title-crafts{
    font-family: 'Tangerine', cursive;
    font-size: 140px;
    font-weight: bold;
    padding-top: 25px;
    color: rgb(44, 44, 143);
}

.home-title-construction{
    font-family: 'Zen Dots', cursive;
    font-size: 55px;
    font-weight: bold;
    padding-top: 100px;
}

.home-title-realty{
    font-family: 'Abril Fatface', cursive;
    font-size: 75px;
    font-weight: bold;
    padding-top: 75px;
    color: rgb(44, 44, 143);
}